			color: inherit;
		}
	}
	.disabled {
		&:hover {
			cursor: not-allowed;
			color: #bdbdbd;
		}
		a {
			&:hover {
				cursor: not-allowed;
				color: #bdbdbd;
			}
		}
	}
	.active {
		color: #fff;
	}
</style>
